import React, { Component } from 'react';
import '../App.css';

class Footer extends Component {
  render() {
    return (
      <footer aria-label="footer" tabIndex="0">
        <div>
          <p>This app as a collection of our favorite locations to hang around in the city. Wish you as lovely memories here as we have!</p>
        </div>
      </footer>
    );
  }
}

export default Footer;



